.loader_holder {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  width:50px;
  height:50px;
  border-radius:50%;
  border:8px solid;
  border-color:#edeae4;
  border-right-color: coral;
  animation:s2 1s infinite linear;
}
@keyframes s2 {to{transform: rotate(1turn)}}